@include font-face("Montserrat", "Montserrat-Bold", 700);
@include font-face("Montserrat", "Montserrat-Medium", 500);
@include font-face("Montserrat", "Montserrat-Regular", 400);
@include font-face("Montserrat", "Montserrat-Light", 300);

body {
  font-family: "Montserrat", Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.25;
  color: var(--color__text);
}

h1,
.--h1 {
  font-size: 3em;
  font-weight: 700;
  text-transform: uppercase;

  @media screen and (max-width: 639px) {
    font-size: 1.25em;
  }
}

h2,
.-h2 {
  font-size: 2.25em;
  font-weight: 700;
  text-transform: uppercase;

  @media screen and (max-width: 639px) {
    font-size: 1em;
  }
}

h3,
.--h3 {
  font-size: 1.5em;
  font-weight: 700;
  text-transform: uppercase;

  @media screen and (max-width: 639px) {
    font-size: 1em;
  }
}

h4,
.--h4 {
  font-size: 1em;
  font-weight: 700;

  @media screen and (max-width: 639px) {
    font-size: 1em;
  }
}