html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
  position: relative;
}

body {
  max-width: 100%;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

button:focus,
input:focus {
  outline: transparent;
}

button {
  @include no-btn;
}

.flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.--just-space {
  justify-content: space-between;
}

.--just-center {
  justify-content: center;
}

.--just-end {
  justify-content: flex-end;
}

.--align-str {
  align-items: stretch;
}

.--align-center {
  align-items: center;
}

.--align-end {
  align-items: flex-end;
}

.container {
  width: 100%;
  max-width: 120rem;
  padding: 0 9.75rem;

  @media screen and (max-width: 1759px) {
    padding: 0 2rem;
  }

  @media screen and (max-width: 639px) {
    padding: 0 1.25rem;
  }
}